}

.display-middle {
  color: #fff;
  white-space: nowrap;
  margin: 0%;
  font-size: 60px;
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.display-middle h1 {
  text-shadow: 4px 4px #777;
}

#heroicon {
  width: 20%;
