  align-items: center;
  margin: auto;
  display: flex;
}

#menuSection article {
  text-align: center;
  border: 3px solid #fff;
  min-width: 23%;
  max-width: 25%;
  margin: 2%;
  padding: .25em .5em;
  box-shadow: 1px 1px, 2px 2px, 3px 3px, 4px 4px, 5px 5px #fff;
}

#bookingForm, #loginForm, #registerForm, #menuForm {
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  display: flex;
}

#menuForm input, #bookingForm input {
  max-width: 100%;
}

#bookingForm input, select, button, #registerForm input, select, button, #loginForm input, select, button, #menuForm input {
  color: #777;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  border: 3px solid #777;
  margin-top: 1%;
  padding: .25em .5em;
  position: relative;
  box-shadow: 1px 1px, 2px 2px, 3px 3px, 4px 4px, 5px 5px #777;
}

#bookingForm button, #registerForm button, #loginForm button, #menuForm button {
  margin-top: 5%;
}

#bookingForm input:active, select:active, button:active, #loginForm input:active, button:active, #registerForm input:active, button:active {
  color: #777;
  top: 5px;
  left: 5px;
  box-shadow: 0 0;
}

button {
  cursor: pointer;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #777;
  width: 100%;
  height: 230px;
  margin-top: 5%;
  padding: 1em 0;
  position: relative;
}

.footer-content {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
  display: flex;
}

.footer-content a {
  color: #fff;
  font-size: 24px;
  text-decoration: none;
}

.footer-content a:hover {
  color: #ddd;
}

.footer-bottom {
  text-align: center;
  width: 100%;
  font-size: 14px;
  position: absolute;
  bottom: 10px;
}

#menuItemsList {
  margin: auto;
}

.edit-menu-icon, .delete-menu-icon, .edit-booking-icon, .delete-booking-icon {
  cursor: pointer;
  vertical-align: middle;
  margin-left: 10px;
  font-size: 24px;
}

.edit-menu-icon:hover, .delete-menu-icon:hover, .edit-booking-icon:hover, .delete-booking-icon:hover {
  color: tomato;
}

#snackbar {
  visibility: hidden;
  color: #fff;
  text-align: center;
  z-index: 1;
  background-color: #333;
  border-radius: 2px;
  min-width: 250px;
  margin-left: -125px;
  padding: 16px;
  position: fixed;
  bottom: 30px;
  left: 50%;
}

#snackbar.show {
  visibility: visible;
  animation: .5s fadein, .5s 2.5s fadeout;
}

@keyframes fadein {
  from {
    opacity: 0;
    bottom: 0;
  }

  to {
    opacity: 1;
    bottom: 30px;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
    bottom: 30px;
  }

  to {
    opacity: 0;
    bottom: 0;
  }
}

@media screen and (width <= 1000px) {
  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    display: block;
  }
}

@media screen and (width >= 1000px) {
  #logoutLink, #loginlink {
    float: right;
  }
}

@media screen and (width <= 1000px) {
  .topnav.responsive {
    position: fixed;
  }

  .topnav.responsive a.icon {
    position: absolute;
    top: 0;
    right: 0;
  }

  .topnav.responsive a {
    float: none;
    text-align: left;
    display: block;
  }
}

@media screen and (width <= 900px) {
  .display-middle h1 {
    font-size: 50px;
  }

  #heroicon {
    display: none;
  }
}

@media screen and (width <= 600px) {
  .parallax1, .parallax2 {
    display: none;
  }
}

@media screen and (width <= 750px) {
  #menuSection article {
    max-width: 60%;
  }
}
